module.exports = "body {\n    background: #384047;\n    font-family: sans-serif;\n    font-size: 10px\n  }\n  \n  form {\n    background: #fff;\n    padding: 4em 4em 2em;\n    max-width: 50%;\n    margin: auto;\n    margin-top: 4%;\n    box-shadow: 0 0 1em #222;\n    border-radius: 5px;\n  }\n  \n  p {\n    margin: 0 0 3em 0;\n    position: relative;\n  }\n  \n  label {\n    display: block;\n    font-size: 1.6em;\n    margin: 0 0 .5em;\n    color: #333;\n  }\n  \n  input {\n    display: block;\n    box-sizing: border-box;\n    width: 100%;\n    outline: none\n  }\n  \n  input[type=\"text\"],\n  input[type=\"password\"],select, input[type=\"file\"], input[type=\"number\"]{\n    background: #f5f5f5;\n    border: 1px solid #e5e5e5;\n    font-size: 1.6em;\n    padding: 10px;\n    border-radius: 5px;\n  }\n  \n  input[type=\"text\"]:focus,\n  input[type=\"password\"]:focus {\n    background: #fff\n  }\n  \n  button[type=\"submit\"] {\n    background: #2F558E;\n    box-shadow: 0 3px 0 0 #1D3C6A;\n    border-radius: 10px;\n    border: none;\n    color: #fff;\n    cursor: pointer;\n    display: block;\n    font-size: 2em;\n    line-height: 1.6em;\n    outline: none;\n    padding: 13px;\n    margin: auto;\n    margin-top: 5%;\n    text-shadow: 0 1px #68B25B;\n  }\n  \n  button[type=\"submit\"]:disabled {\n    background:#8dc0c1;\n}\n"